import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/iphone",
    name: "iphone",
    component: () => import("../views/IphoneView.vue"),
  },
  {
    path: "/poco",
    name: "poco",
    component: () => import("../views/PocoView.vue"),
  },
  {
    path: "/ipad",
    name: "ipad",
    component: () => import("../views/IpadView.vue"),
  },
  {
    path: "/mac",
    name: "mac",
    component: () => import("../views/MacView.vue"),
  },
  {
    path: "/oppo",
    name: "oppo",
    component: () => import("../views/OppoView.vue"),
  },
  {
    path: "/samsung",
    name: "samsung",
    component: () => import("../views/SamsungView.vue"),
  },
  {
    path: "/watch",
    name: "watch",
    component: () => import("../views/WatchView.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
